import * as React from 'react'
import type { HeadFC, PageProps } from 'gatsby';
import { Button, Container, Form, InputGroup } from 'react-bootstrap';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faEnvelope } from '@fortawesome/free-solid-svg-icons';

import Navigation from '../components/navigation';
import Footer from '../components/footer';

import love2CraftBgImage from '../images/love2craft-story-bg.png';

const ContactMePage: React.FC<PageProps> = () => {
  return (
    <>
      <Navigation  />    
      <main>
        <div className='d-flex py-5'>
          <Container className='flex-fill d-flex my-story-container' style={{ 
            backgroundImage: `url(${love2CraftBgImage})`,
            backgroundRepeat: 'no-repeat',
            backgroundSize: 'cover',
            backgroundPosition: 'center',
            minHeight: '600px',
            paddingRight: '0px',
            paddingTop: '0px'

          }}>
            <div className='my-story flex-fill'>
              <div className='row'>
                <div className='p-4 col-12 col-lg-6 col-xl-5 col-xxl-4'>                  
                  <h1>Get in Touch</h1>                
                  <div>
                    <p>Have a question about our handmade items, shipping, or custom order? We're always happy to help!</p>
                    <p>Whether you're curious about materials, care instructions, or upcoming designs, feel free to reach out.</p>
                    <p>Love2CraftLLC values every customer and is here to make your experience as warm as personal as the crafts themselves.</p>
                  </div>                
                  <div className='py-2'>
                    <p>Tailored to your preferences, our custom creations bring your vision to life through resin art, crochet, and seasonal d√©cor.</p>
                    <p>Each piece is a reflection of your individual style, meticulously crafted by Love2CraftLLC to create a one-of-a-kind masterpiece.</p>
                  </div>
                  <div style={{ 
                    backgroundColor: 'white',
                    border: '1px solid black'
                  }}>
                    <p className='p-2'>If you have any inquiries, please feel free to reach out to us!</p>

                    <div className='p-2'>
                      <span> <FontAwesomeIcon icon={faEnvelope} /></span>
                      <span>Email: <a href="mailto:love2craft@gmail.com"></a>love2craft@gmail.com</span>
                    </div>
                  </div>
                </div>                
                <div className='p-4 col-12 col-lg-2 col-xl-2 col-xxl-1'>
                  &nbsp;
                </div>
                <div className='p-4 col-12 col-lg-4 col-xl-5 col-xxl-7'>
                  <Form>
                    <Form.Group className="mb-3" controlId="exampleForm.ControlInput1">
                      <Form.Label>Name</Form.Label>
                      <Form.Control type="name" />
                    </Form.Group>
                    <Form.Group className="mb-3" controlId="exampleForm.ControlInput2">
                      <Form.Label>Phone</Form.Label>
                      <Form.Control type="phone" placeholder="(123) 123-4567" />
                    </Form.Group>
                    <Form.Group className="mb-3" controlId="exampleForm.ControlTextarea1">
                      <Form.Label>Message *</Form.Label>
                      <Form.Control as="textarea" rows={5} />
                    </Form.Group>                    
                    <Button variant='light'>Send</Button>
                  </Form>
                </div>                
              </div>
            </div>
          </Container>

        </div>
      </main>
      <Footer />
    </>
  )
}

export default ContactMePage

export const Head: HeadFC = () => <title>Contact Me</title>